import React from "react";
import {StyleSheet, View, Text} from "react-native";

export default function Header() {
    return (
        <View style={styles.main}>
            <Text style={styles.text}> </Text>
        </View>
    );  
}

const styles = StyleSheet.create({
    main: {

    },
    text: {
        marginTop: 40,
        marginBottom: 40,
        color: "#168af0",
        textAlign: "center",
        fontSize: "40px",
        fontWeight: "bold",
    },
});

export {Header};