import React from "react";
import { useState } from "react";
import { StyleSheet, TextInput, Text, TouchableOpacity, View } from "react-native";

export default function Form({ addHandler }) {
    const [text, setValue] = useState("");
    const onChange = (text) => {
        setValue(text);
    };

    return (
        <View style={styles.container}>
            <TextInput
                style={styles.input}
                onChangeText={onChange}
                placeholder=" "
            />
            <TouchableOpacity
                style={styles.btn}
                onPress={() => addHandler(text)}
            >
                <Text style={styles.btnText}> </Text>
            </TouchableOpacity>
        </View>
    );
}

const styles = StyleSheet.create({
    container: {
        flex: 1,
        justifyContent: 'center',
        alignItems: 'center',
        marginRight: "20%",
        marginLeft: "20%",
        gap: "20px",
        marginBottom: "40px"
    },
    input: {
        borderRadius: 12,
        width: "100%",
        fontSize: 20,
        padding: 20,
        borderWidth: 2,
        borderColor: '#bfe1ff'
    },
    btn: {
        backgroundColor: "#168af0",
        width: "100%",
        padding: 10,
        borderRadius: 5,
        alignItems: 'center',
        borderRadius: 12,
        padding: 20,
    },
    btnText: {
        color: '#ffffff',
        fontSize: 20
    }
});


export {Form};